import React, { Component } from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import Scrollspy from 'react-scrollspy';
import PropTypes from "prop-types";
import { Link } from "gatsby"
import ExternalLink from '@common/ExternalLink';

import { Container } from '@components/global';
import {
  Nav,
  SubNav,
  NavItem,
  Brand,
  SubBrand,
  StyledContainer,
  SubStyledContainer,
  NavListWrapper,
  MobileMenu,
  Mobile,
  Transition,
  Indiegogo,
} from './style';

import { ReactComponent as MenuIcon } from '@static/icons/menu.svg';
import { ReactComponent as MenuLogo } from '@static/menulogo.svg';
import { ReactComponent as ProductLogo } from '@static/productlogo.svg';


const NAV_ITEMS = ["關於（英）", "Hai", "聯繫（英）"];
const SUBNAV_ITEMS = [["簡介","Intro"],["如何使用","Usage"],["技術特色","Features"],["訂閱我們","Subscribe"]];
const SUBNAV_ITEMS_SPY = ["Intro", "Usage","Features", "Subscribe"];

class Navbar extends Component {
  state = {
    mobileMenuOpen: false,
  };
  static propTypes = {
    brand: PropTypes.shape({
      name: PropTypes.string.isRequired,
      to: PropTypes.string.isRequired
    }),
    links: PropTypes.arrayOf(
      PropTypes.shape({
        name: PropTypes.string.isRequired,
        to: PropTypes.string.isRequired
      })
    )
  };

  constructor(props) {
    super(props);
    this.state = {
      show: false,
      scrollPos: 0,
      threshold: false,
    };
    this.handleScroll = this.handleScroll.bind(this);
  }

  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  handleScroll() {
    const { scrollPos } = this.state;

      this.setState({
        scrollPos: document.body.getBoundingClientRect().top,
        show: document.body.getBoundingClientRect().top < scrollPos && scrollPos < -50,
      }); 
      console.log(this.state.show) 
    
  }

  toggleMobileMenu = () => {
    this.setState(prevState => ({ mobileMenuOpen: !prevState.mobileMenuOpen }));
  };

  closeMobileMenu = () => {
    if (this.state.mobileMenuOpen) {
      this.setState({ mobileMenuOpen: false });
    }
  };

  getNavAnchorLink = item => (
    <AnchorLink href={`#${item.toLowerCase()}`} onClick={this.closeMobileMenu}>
      {item}
    </AnchorLink>
  );

  getSubNavAnchorLink = item => (
    <AnchorLink href={`#${item[1].toLowerCase()}`} onClick={this.closeMobileMenu}>
      {item[0]}
    </AnchorLink>
  );

  getNavList = ({ mobile = false }) => (
    <NavListWrapper mobile={mobile}>
      <Scrollspy>
      {NAV_ITEMS.map(navItem => (
        <NavItem key={navItem}><Link to={`${navItem.toLowerCase()}`}>{navItem}</Link></NavItem>
      ))}
      </Scrollspy>
    </NavListWrapper>
  );

  getSubNavList = ({ mobile = false }) => (
    <NavListWrapper mobile={mobile}>
      <Scrollspy
        items={SUBNAV_ITEMS_SPY.map(item => item.toLowerCase())}
        currentClassName="active"
        mobile={mobile}
        offset={-64}
      >
        {SUBNAV_ITEMS.map(navItem => (
          <NavItem key={navItem[1]}>{this.getSubNavAnchorLink(navItem)}</NavItem>
        ))}
        <NavItem key="OurIndiegogo">
          <ExternalLink href="https://www.indiegogo.com/projects/hai-world-s-first-bluetooth-earphones-for-all/coming_soon/x/23571765">
            <Indiegogo>Indiegogo 預訂 <span role="img" aria-label="heart">💖</span></Indiegogo>
          </ExternalLink>
        </NavItem>
      </Scrollspy>
    </NavListWrapper>
  );

  render() {
    const { mobileMenuOpen } = this.state;
    return (
      <div>
      <Transition>
      <Nav className={this.state.show ? "hidden" : "active"} {...this.props}>
        <StyledContainer>
          <Brand>
            <Link to="/"><MenuLogo/></Link>
          </Brand>
          <Mobile>
            <button onClick={this.toggleMobileMenu} style={{ color: 'black' }}>
              <MenuIcon />
            </button>
          </Mobile>
          <Mobile hide>{this.getNavList({})}</Mobile>
        </StyledContainer>
        <Mobile>
          {mobileMenuOpen && (
            <MobileMenu>
              <Container>{this.getNavList({ mobile: true })}</Container>
            </MobileMenu>
          )}
        </Mobile>
      </Nav>
      
      <SubNav className={this.state.show  ? "active_down" : "active_up"} toggleSubNav={this.props.toggleSubNav}>
        <SubStyledContainer>
            <Mobile hide>{this.getSubNavList({})}</Mobile>
            <SubBrand>
              <Link to="/hai"><ProductLogo/></Link>
            </SubBrand>
        </SubStyledContainer>
      </SubNav>
      </Transition>
      </div>
    );
  }
}

export default Navbar;
