import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Fade from 'react-reveal/Fade';

import { Section, Container } from '@components/global';
import SectionHeader from '@ui/sectionheader';

import gifFeatures from '@images/art/gif_features.gif';

const SPECS = [
  {
    name: '優異音質',
    image: 'icon-07',
    role: '由聲學工程專家研製，與助聽器跟電子耳的 T 模式都相容。',
  },
  {
    name: '動態噪訊消除（Dynamic Buzz Cancellation）',
    image: 'icon-02',
    role: '創新技術，只為消除電磁噪音而生。解決 T 模式下助聽器/電子耳使用者的共同困擾。',
  },
  {
    name: '直覺好用',
    image: 'icon-03',
    role: '三鍵介面超直覺，讓你調整音量、換歌、接電話、或喚醒你的智慧手機助理。',
  },
  {
    name: '通用設計',
    image: 'icon-04',
    role: '模組化、可置換的線圈跟入耳式耳機單體，讓助聽器、電子耳、聽常者都能使用。單側助聽器使用者也能輕鬆上手。',
  },
  {
    name: '用 App 調整等化器',
    image: 'icon-05',
    role: 'Hai App 讓你改變等化器音效，還可以為你的不同聆聽環境，儲存你最愛的設定。',
  },
  {
    name: '走到哪，用到哪',
    image: 'icon-06',
    role: '輕量、耐水，你可以把 Hai 戴在胸前，也可以夾在衣服上。',
  },
  {
    name: '六小時無線續航',
    image: 'icon-01',
    role: '整天工作，無線續航。若兩側都使用耳道式單元，連續使用時間最高可達 30 小時。',
  },
];



const TechSpec = () => (
  <StaticQuery
    query={graphql`
      query {
        Features: file(
          sourceInstanceName: { eq: "spec" }
          name: { eq: "features" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
      }
    `}
    render={data => (
      <Section id="features" accent="secondary">
        <SectionHeader header="產品特色" subheader="" />
        <Container style={{ position: 'relative' }}>
          <Grid>
            <SpecMainImg>
              <Fade>
                <Art>
                <img src={gifFeatures} alt="Product Hai"/>
                </Art>
              </Fade>
            </SpecMainImg>
            <SpecGrid>
              {SPECS.map(({ name, image, role }) => {

                return (
                  <div>
                  <SpecItem>
                    <Art>
                      <img key={image} src={require(`@images/spec/${image}.png`)}  alt="tech spec of Hai"/>
                    </Art>
                    <div>
                      <Fade>
                      <Title>{name}</Title>
                      <p>{role}</p>
                      </Fade>
                    </div>
                  </SpecItem>
                  </div>
                );
              })}
            </SpecGrid>
          </Grid>
        </Container>
      </Section>
    )}
  />
);


const Grid = styled.div`
  display: grid;
  grid-template-columns: 3fr 5fr;
  grid-gap: 50px;
  text-align: left;
  align-items: top;
  justify-items: left;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 96px;
    &:last-child {
      margin-bottom: 24px;
    }
  }
`;

const SpecMainImg = styled.div`
  height: 100%;
  width: 100%;
  @media (max-width: ${props => props.theme.screen.md}) {
    width: 50%;
    margin: 0 auto;
  }
`;

const SpecGrid = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content;
  grid-gap: 30px;
  justify-content: space-between;
  width: 100%;
  margin-top: 32px;

  @media (max-width: ${props => props.theme.screen.lg}) {
    justify-content: start;
  }

  @media (max-width: ${props => props.theme.screen.md}) {
    width: 100%;
    grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
  }

  @media (max-width: ${props => props.theme.screen.xs}) {
    grid-gap: 24px;
  }
`;

const SpecItem = styled.div`
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-gap: 30px;
  text-align: left;
  align-items: top;
  justify-items: left;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr 2fr;
    text-align: left;
    margin-bottom: 36px;

    &:last-child {
      margin-bottom: 24px;
    }
  }
  @media (max-width: ${props => props.theme.screen.xs}) {
    margin-bottom: 5px;
  }
`;

const Art = styled.figure`
  margin: 0;
  max-width: 1380px;
  width: 100%;
  img{
    width: 100%;
  }
`;

const Title = styled.p`
  margin-bottom: 10px;
  color: ${props => props.theme.color.black.regular};
  font-weight: bold;
`;

export default TechSpec;
