import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import Fade from 'react-reveal/Fade';
import styled from 'styled-components';

import {Section, Container} from '@components/global';
import SectionHeader from '@ui/sectionheader';
import step2 from '@images/art/step02.gif';
import step3 from '@images/art/step03.gif';

const STEPS = [
  {
    header: '藍芽連線',
    text: '讓 Hai 用藍芽連上你的任何音源（例如手機、電腦、平板、甚至可以用藍牙轉接頭連接Switch！）',
  },
  {
    header: '打開T模式',
    text: "打開你助聽輔具的T模式。Hai的DBC降噪科技可以為你消除周圍的磁場噪訊",
  },
  {
    header: '享受美聲',
    text: "Hai 的天線或耳機，讓你不論有沒有使用助聽輔具，都可享受美聲",
  },
];

const Manual = () => (
  <StaticQuery
    query={graphql`
      query {
        Modularity: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "modularity_s" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Mobile: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "step01" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
      }
    `}
    render={data => (
      <Section id="usage" accent="primary">
        <SectionHeader 
          header="如何使用" 
          subheader=""
          bg="inverse"
        />
        <Container>
          <Wrapper>
              <Grid>
                <div>
                      <Img fluid={data.Mobile.childImageSharp.fluid} />
                      <h3><i>1.</i> {STEPS[0].header}</h3>
                      <p>{STEPS[0].text}</p>
                      
                </div>
                <div>
                      <img  src={step2} alt="step2"/>
                      <h3><i>2.</i> {STEPS[1].header}</h3>
                      <p>{STEPS[1].text}</p>
                </div>
                <div>
                      <img  src={step3} alt="step3"/>
                      <h3><i>3.</i> {STEPS[2].header}</h3>
                      <p>{STEPS[2].text}</p>
                </div>
                
              </Grid>
              <GridNote>
                <div>
                  <Img fluid={data.Modularity.childImageSharp.fluid} />
                </div>
                <div> 
                  <h3><small>切換如此容易</small></h3>
                  <p><small>
                    ✅  如果你只使用單側助聽輔具，你可以為 Hai 裝上一個單側耳機，這樣優耳的那側，就可以用它聆聽。而配戴助聽器/電子耳的另一側，則使用 Hai 的天線。<br/>
                    ✅  如果你想拿下你的助聽器/電子耳休息一下，就為 Hai 裝上兩個耳機，用 App 調整 Hai 的聲音以適應你的聽力曲線。
                    </small></p>
                </div>
              </GridNote>
          </Wrapper>
        </Container>
      </Section>
    )}
  />
);



const Wrapper = styled.div`
  padding: 60px 0 40px 0;
  font-weight: 400;
  vertical-align: middle;
  color: ${props => props.theme.color.grayscale.S100};
  width: 100%;
  @media (max-width: ${props => props.theme.screen.md}) {
    max-width: 100%;
    padding: 0 0 60px 0;
  }
  @media (max-width: ${props => props.theme.screen.sm}) {
    padding: 0 0 40px 0;
  }
  @media (max-width: ${props => props.theme.screen.xs}) {
    padding: 0 0 0 0;
  }
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: 2fr 2fr 2fr;
  grid-gap: 25px;
  text-align: left;
  align-items: top;
  justify-items: left;
  padding-bottom: 15px;
  img{
    width: 100%;
  }
  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    grid-gap: 15px;
    &:last-child {
      margin-bottom: 24px;
    }
  }
`;


const GridNote = styled.div`
  display: grid;
  grid-template-columns: 1fr 7fr;
  grid-gap: 20px;
  text-align: left;
  align-items: top;
  justify-items: left;
  padding-bottom: 15px;
  border-top: 1px solid  ${props => props.theme.color.supplementary.S700};
  padding-top: 35px;
  margin-top: 10px;
  div{
    width: 100%;
  }
  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    grid-gap: 15px;
    &:last-child {
      margin-bottom: 24px;
    }
    display: none;
  }
`;


export default Manual;
