import styled from 'styled-components';

import { Container } from '@components/global';

export const Nav = styled.nav`
  padding: 6px 0px 14px 0;
  background-color: ${props => props.theme.color.background.S100};
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
  @media (max-width: ${props => props.theme.screen.md}) {
    padding: 7px 0px 11px 0;
  }
`;

export const SubNav = styled.nav`
  padding: 3px 0px 6px 0;
  background-color: ${props => props.theme.color.background.S200};
  border-bottom: ${props => props.theme.color.background.S300} 1px solid;
  position: fixed;
  width: 100%;
  top: 55px;
  z-index: 1000;
  display:none;
  ${props =>
    props.toggleSubNav &&
    `display: ${
      props.toggleSubNav === 'true'
        ? 'block'
        : 'none'
    }`};
  @media (max-width: ${props => props.theme.screen.md}) {    
    display: none;
  }

`;

export const StyledContainer = styled(Container)`
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: ${props => props.theme.font.primary};
  font-weight: bold;
  ${props => props.theme.font_size.small};
`;

export const SubStyledContainer = styled(Container)`
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  font-family: ${props => props.theme.font.secondary};
  ${props => props.theme.font_size.small};
`;

export const NavListWrapper = styled.div`
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;

    ${({ mobile }) =>
      mobile &&
      `
        flex-direction: column;
        margin-top: 1em;
        :last-child{
          margin-bottom: 3.75em;
        }

        > ${NavItem} {
          margin: 0;
          padding: 10px 0 20px 0;
          margin-top: 0.75em;
          border-bottom: gray 1px solid;
        }
      `};
  }
`;

export const NavItem = styled.li`
  margin: 0 0.75em;

  a {
    text-decoration: none;
    color: ${props => props.theme.color.grayscale.S1000};
  }
  a:hover{
    color: ${props => props.theme.color.secondary.S400};
  }

  &.active {
    a {
      font-weight: bold;
    }
  }
`;

export const MobileMenu = styled.div`
  width: 100%;
  background: ${props => props.theme.color.primary};
`;

export const Brand = styled.div`
  margin-top: 10px;
  width: 100px;
  @media (max-width: ${props => props.theme.screen.md}) {
    margin-top: 10px;
    width: 90px;
  }
`;

export const SubBrand = styled.div`
  margin-top: 8px;
  width: 45px;
`;

export const Indiegogo = styled.div`
  color: ${props => props.theme.color.secondary.S600};
  padding: 0 0 0 20px;
  border-left:  1px solid ${props => props.theme.color.background.S300};
`;



export const Mobile = styled.div`
  display: none;

  @media (max-width: ${props => props.theme.screen.md}) {
    display: block;
    padding-top:5px;
    padding-bottom: 0px;
  }

  ${props =>
    props.hide &&
    `
    display: block;

    @media (max-width: ${props.theme.screen.md}) {
      display: none;
    }
  `}
`;

export const Transition = styled.div`
  .active {
    visibility: visible;
    transition: all 200ms ease-in;
  }
  .hidden {
    visibility: hidden;
    transition: all 200ms ease-out;
    transform: translate(0, -100%);
  }
  .active_up{
    transition: all 200ms ease-in;
  }
  .active_down{
    background-color: ${props => props.theme.color.background.S100};
    border-bottom: ${props => props.theme.color.background.S200} 1px solid;
    transition: all 200ms ease-out;
    transform: translate(0, -130%);
  }
`;