import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Section} from '@components/global';
import SectionHeader from '@ui/sectionheader';
import ImgTxt from '@ui/imgtxt';
import ImgTxtInverse from '@ui/imgtxt_inverse';
import ImgSingle from '@ui/img_single';
//import Video from '@ui/video';


const Intro2 = () => (
  <StaticQuery
    query={graphql`
      query {
        Earphone4all: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "earphone4all" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Unidesign: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "unidesign" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Music: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "music" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Noise: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "noise" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Mobileeq: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "mobileeq" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Ui: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "ui" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Conversation: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "conversation" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Stickers: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "stickers" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Anywhere: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "parkour" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
      }
    `}
    render={data => (
      <Section id="intro" padding="tight">
        <SectionHeader 
          header="還有更多！" 
        />
          
        <ImgSingle
          header="用 App 調出你要的聲音" 
          text="配合我們的 Hai 手機應用程式，可以做更多！你可以用等化器讓聲音更好聽，還可以儲存各種設定，在不同環境快速切換。在接電話時強化人聲，或改變兩邊環繞音效，來適應你的聽力狀況" 
          togglecover="true"  
          toggletop="false"       
        >
           <Img fluid={data.Mobileeq.childImageSharp.fluid} />
        </ImgSingle>
        <ImgTxt 
          header="不再聽見嗡嗡嗡" 
          text="我們對聆聽體驗近乎苛求。針對助聽器在 Telecoil 模式下會聽到的環境干擾，我們開發世界第一的降噪技術——動態噪訊消除（Dynamic Buzz Cancellation）。它讓你享受音樂，不被飛機上或變電箱附近的怪聲打擾" 
        >
          <Img fluid={data.Noise.childImageSharp.fluid} />
        </ImgTxt> 
        <ImgTxtInverse 
          header="對話清晰，如在面前" 
          text="用 Hai 打給你的愛人、朋友，或是客戶吧。我們的內建麥克風清晰動聽，延遲極低" 
        >
          <Img fluid={data.Conversation.childImageSharp.fluid} />
        </ImgTxtInverse>
        <ImgTxt
          header="無比直覺流暢" 
          text="為容易上手而生 —— 我們的三個主要按鍵讓你輕鬆接電話、切歌、還有調整音量。你還可以直接用 Hai 跟 Google Assistant 與 Siri 說話！"    
        >
          <Img fluid={data.Ui.childImageSharp.fluid} />
        </ImgTxt>
        <ImgSingle
          header="今日我最美" 
          text="用不同的外殼跟貼皮客製化你的 Hai！讓你的創意自由飛翔，讓你的 Hai 是你的 style"
          togglecover="true"  
          toggletop="false"       
        >
          <Img fluid={data.Stickers.childImageSharp.fluid} />
        </ImgSingle>
        <ImgTxt
          header="無論晴雨，都陪伴你" 
          text="無論天氣好壞，你都可以帶著它。它很輕，可以夾在衣服上 —— 或就像個項鍊戴在胸前！防水又現代的設計，讓你不論運動、工作、或只是在耍廢，都時時動聽"  
        >
          <Img fluid={data.Anywhere.childImageSharp.fluid} />
        </ImgTxt> 
      </Section>
    )}
  />
);

export default Intro2;
